@use './scss/' as *;

@import url('https://fonts.googleapis.com/css2?family=Rowdies:wght@300;400;700&display=swap');

*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  -webkit-tap-highlight-color: transparent;
  user-select: none;
}

body,html{
  font-size: 62.5%;
  position: relative;
}
body{
  font-family: $font-family;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;
  background: $black;
  color: $white;
  overflow: hidden;
}

ul,ol,ul{
  list-style: none;
}

a{
  text-decoration: none;
  color: unset;
}

iframe {
  border: 0;
}

#root,
.App {
  height: 100vh;
}

.bg-image{
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.overlay{
  position: relative;
  &::before{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba($color: #000000, $alpha: .8);
  }
}

.main-color{
  color: $main-color;
}

.second-color{
  color: $second-color;
}

.title{
  font-weight: 700;
  span{
    font-size: 3rem;
  }
  h2{
    font-size: 5rem;
    line-height: 5rem;
  }
}

.container{
  max-width: 1240px;
  margin: auto;
}

.mt-4{
  margin-top: 4rem;
}

.relative{
  position: relative;
}

.swiper,
.swiper-slide{
  height: 100%;
}